import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Footer from '../Footer/Footer';
import { NavLink } from 'react-router-dom';
import './Emergency.css';

const Emergency = () => {

    const [emergencyContacts, setEmergencyContacts] = useState({});
    const [regEmergencyContacts, setRegEmergencyContacts] = useState([]);
    const [dataReceived, setDataReceived] = useState(false);

    useEffect(() => {
        if (!dataReceived) {
            axios.get("https://api.rootnet.in/covid19-in/contacts").then(
                response => {
                    setEmergencyContacts(response.data.data.contacts.primary);
                    setRegEmergencyContacts(response.data.data.contacts.regional);
                    setDataReceived(true);
                }
            )
        }
    });

    return (
        <div>
            <div className="container-fluid">
                <h1 className="contact-heading">Emergency Contacts</h1>
                <h2>Ministry of Health and Family Welfare, Government of India <strong>(MoHFW)</strong></h2>
                <div className="emer-contact">
                    <p>Toll Free Number: <a href={"tel:" + emergencyContacts['number-tollfree']}>{emergencyContacts["number-tollfree"]}</a></p>
                    <p>Phone Number: <a href={"tel:" + emergencyContacts.number}>{emergencyContacts.number}</a></p>
                    <p>Email ID: <a href={"mailto:" + emergencyContacts.email}>{emergencyContacts.email}</a></p>
                    <p>Follow MoHFW Social Media Pages: </p>
                    <a href="https://www.facebook.com/MoHFWIndia" rel="noopener noreferrer" target="_blank"><button className="btn btn-primary social-media-button">Facebook</button></a>
                    <a href="https://twitter.com/MoHFW_INDIA" rel="noopener noreferrer" target="_blank"><button className="btn btn-primary social-media-button">Twitter</button></a>
                </div>
                <div className="row">
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <td>State/UT</td>
                                <td>Contact</td>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                regEmergencyContacts.map((data) => {
                                    return (
                                        <tr key={data.loc}>
                                            <td>{data.loc}</td>
                                            <td><a href={"tel:" + data.number}>{data.number}</a></td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
                <div align="center">
                    <NavLink exact to="/">
                        <button style={{ fontSize: "15px", marginTop: "10px" }} className="btn btn-primary">Home</button>
                    </NavLink>
                </div>
            </div>
            <Footer />
        </div>
    )
}

export default Emergency;