import React from 'react';
import { NavLink } from 'react-router-dom';

const Page404 = () => {
    return (
        <div align="center">
            <h1>Oops! Requested page not found.</h1>
            <h2>Abort mission and return home. Over!</h2>
            <div>
                <NavLink exact to="/">
                    <button style={{fontSize: "20px"}} className="btn btn-primary">Home</button>
                </NavLink>
            </div>
        </div>
    )
}

export default Page404;