import React, { useEffect, useState } from 'react';
import './Home.css';
import axios from 'axios';
import Graph from '../Graph/Graph';
import Map from '../Map/Map';
import Footer from '../Footer/Footer';

const Home = (props) => {
    const [dataReceived, setDataReceived] = useState(false);
    const [total, setTotal] = useState({});
    const [stateData, setStateData] = useState([]);
    const [stateDistData, setStateDistData] = useState([]);
    const [stateDistArr, setStateDistArr] = useState([]);
    const [graphData, setGraphData] = useState([]);

    useEffect(() => {
        const getStates = async () => {
            const [response, stateDistResponse] = await Promise.all([
                axios.get('https://api.covid19india.org/data.json'),
                axios.get('https://api.covid19india.org/state_district_wise.json')]);
            setTotal(response.data.statewise[0]);
            setStateData(response.data.statewise.slice(1).sort((a, b) => {
                return (b.confirmed - a.confirmed);
            }));
            setGraphData(response.data.cases_time_series);
            setStateDistData(stateDistResponse.data);
            setDataReceived(true);
            setStateDistArr(Object.entries(stateDistData));
        }
        if (!dataReceived) {
            getStates();
        }
    });

    const calculateActiveCases = (confirmed, recovered, deaths) => {
        return (confirmed - recovered - deaths);
    }

    const getDeltaConfirmed = (deltaconfirmed) => {
        if (deltaconfirmed != 0) {
            return <span className="glyphicon glyphicon-arrow-up delta-confirmed">{deltaconfirmed}</span>;
        }
        return '';
    }

    const getDistDeltaConfirmed = (deltaconfirmed) => {
        if (deltaconfirmed != 0) {
            return <span className="glyphicon glyphicon-arrow-up delta-confirmed">{deltaconfirmed}</span>;
        }
        return '';
    }

    const getDeltaRecovered = (deltaRecovered) => {
        if (deltaRecovered != 0) {
            return <span className="glyphicon glyphicon-arrow-up delta-recovered">{deltaRecovered}</span>;
        }
        return '';
    }

    const getDeltaDeaths = (deltaDeaths) => {
        if (deltaDeaths != 0) {
            return <span className="glyphicon glyphicon-arrow-up delta-deaths">{deltaDeaths}</span>;
        }
        return '';
    }

    const getAffectedStateCount = () => {
        let count = 0;
        stateData.map((state) => {
            if (state.confirmed != 0) {
                count += 1;
            }
        });
        return count;
    }

    const scrollToTop = () => {
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    }

    const onClickHandler = (e) => {
        const hiddenEl = e.currentTarget.nextElementSibling.firstChild.firstElementChild.classList;
        const plusMinusIcon = e.currentTarget.firstElementChild.firstChild.classList;
        (hiddenEl.value.indexOf("collapse") !== -1) ? hiddenEl.remove("collapse") : hiddenEl.add("collapse");
        (plusMinusIcon.value.indexOf("chevron-right") != -1) ? plusMinusIcon.replace("glyphicon-chevron-right", "glyphicon-chevron-down") : plusMinusIcon.replace("glyphicon-chevron-down", "glyphicon-chevron-right");
    }

    const getCoronaFree = () => {
        let coronaFreeStates = "";
        stateData.map((a) => {
            if(a.confirmed !== "0" && a.active === "0") {
                coronaFreeStates += ", " + a.state;
            }
        });
        return coronaFreeStates.substr(2);
    }

    return (
        <div>
            <div className="container-fluid">
                <div className="corona-free">
                    Corona Free States: {getCoronaFree()}
                </div>
                <div className="row">
                    <div className="col-sm-12 heading">Covid 19 - Cases in India</div>
                    <div className="col-sm-12 last-updated">Last Updated: {total.lastupdatedtime} IST</div>
                </div>
                <div className="row">
                    <div className="col-sm-3 col-6 heading-data confirmed">
                        <h4>Confirmed</h4>
                        <p className="count">{total.confirmed}</p>
                        <p className="increment">[+{total.deltaconfirmed}]</p>
                    </div>
                    <div className="col-sm-3 col-6 heading-data active">
                        <h4>Active Cases</h4>
                        <p className="count">{calculateActiveCases(total.confirmed, total.recovered, total.deaths)}</p>
                    </div>
                    <div className="col-sm-3 col-6 heading-data recovered">
                        <h4>Recovered</h4>
                        <p className="count">{total.recovered}</p>
                        <p className="increment">[+{total.deltarecovered}]</p>
                    </div>
                    <div className="col-sm-3 col-6 heading-data deaths">
                        <h4>Deaths</h4>
                        <p className="count">{total.deaths}</p>
                        <p className="increment">[+{total.deltadeaths}]</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <div className="row">
                            <div className="col-sm-12">
                                <Map data={stateData} />
                            </div>
                            <div className="col-sm-12">
                                <Graph data={graphData} />
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-6">
                        <h4 className="affected-states">No. of States/UT affected: {getAffectedStateCount()}</h4>
                        <div id="no-more-tables">
                            <table className="table table-parent">
                                <thead>
                                    <tr>
                                        <th scope="col">State/UT</th>
                                        <th scope="col">Confirmed</th>
                                        <th scope="col">Active</th>
                                        <th scope="col">Recovered</th>
                                        <th scope="col">Deaths</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {stateData.map((state) => {
                                        return (
                                            <React.Fragment>
                                                <tr key={state.state} onClick={onClickHandler}>
                                                    <td><span className="glyphicon glyphicon-chevron-right plus-minus"></span> {state.state}</td>
                                                    <td>{getDeltaConfirmed(state.deltaconfirmed)} {state.confirmed}</td>
                                                    <td>{state.active}</td>
                                                    <td>{getDeltaRecovered(state.deltarecovered)} {state.recovered}</td>
                                                    <td>{getDeltaDeaths(state.deltadeaths)} {state.deaths}</td>
                                                </tr>
                                                <tr key={state.statecode}>
                                                    <td colSpan="5" className="hiddenRow">
                                                        <div className="collapse">
                                                            <h5 className="dist-heading">Last Updated: {state.lastupdatedtime} IST</h5>
                                                            <div>{state.statenotes}</div>
                                                            <table className="table-child">
                                                                <thead>
                                                                    <tr>
                                                                        <th>District</th>
                                                                        <th>Confirmed</th>
                                                                    </tr>
                                                                </thead>
                                                                <tbody>
                                                                    {
                                                                        stateDistArr.map((g) => {
                                                                            if (g[0] === state.state) {
                                                                                return (
                                                                                    Object.entries(g[1].districtData).map((l) => {
                                                                                        return (
                                                                                            <tr key={l[0]}>
                                                                                                <td>{l[0]}</td>
                                                                                                <td>{getDistDeltaConfirmed(l[1].delta.confirmed)} {l[1].confirmed}</td>
                                                                                            </tr>
                                                                                        )
                                                                                    })
                                                                                )
                                                                            }
                                                                        })
                                                                    }
                                                                </tbody>
                                                            </table>
                                                        </div>
                                                    </td>
                                                </tr>
                                            </React.Fragment>
                                        )
                                    })}
                                    <tr>
                                        <th scope="col">Total</th>
                                        <th scope="col">{getDeltaConfirmed(total.deltaconfirmed)} {total.confirmed}</th>
                                        <th scope="col">{total.active}</th>
                                        <th scope="col">{getDeltaRecovered(total.deltarecovered)} {total.recovered}</th>
                                        <th scope="col">{getDeltaDeaths(total.deltadeaths)} {total.deaths}</th>
                                    </tr>
                                </tbody>
                            </table>

                        </div>
                    </div>
                </div>
            </div>
            <div align="center">
                <button className="btn btn-primary go-to-top" onClick={scrollToTop}>Go to Top</button>
            </div>
            <Footer />
        </div>
    );
}

export default Home;