import React from 'react';
import './FAQ.css';
import Footer from '../Footer/Footer';
import { NavLink } from 'react-router-dom';

const FAQ = () => {
    return (
        <div>
            <div className="container-fluid">
                <h1 className="faq-heading">FAQ</h1>
                <div>
                    <div className="faq-question">Is this an official website?</div>
                    <div className="faq-answer">No, this is not an official website. This is my personal project and I've deployed
                    this application on GitHub just to spread awareness about the CoronaVirus cases in India.</div>
                    <div className="faq-question">Who are you?</div>
                    <div className="faq-answer">I'm a software engineer working in Bangalore.</div>
                    <div className="faq-question">What technologies have you used for this project?</div>
                    <div className="faq-answer">I've used React JS, BootStrap, HTML, CSS, JavaScript and JQuery.
                    I've used SVG Map and React graphs to display data.</div>
                    <div className="faq-question">Which sources have you used to collect data for this project?</div>
                    <div className="faq-answer">I've taken data from different APIs provided by COVID19INDIA
                    (https://api.covid19india.org/) and Rootnet (https://api.rootnet.in/covid19-in/contacts).</div>
                    <div className="faq-question">Why is the data shown on this site slightly different from other sources?</div>
                    <div className="faq-answer">Yes, the data shown here is slightly differnt than the other sources
                    and that's because the APIs used are updated based on the latest local/national news. There are no data changes 
                    done from my side.</div>
                    <div className="faq-question">How can I contact you?</div>
                    <div className="faq-answer">You can contact me by sending me a DM on LinkedIn.
                    (https://www.linkedin.com/in/deepak-maddheshiya/)</div>
                </div>
                <div align="center">
                    <NavLink exact to="/">
                        <button style={{ fontSize: "15px", marginTop: "10px" }} className="btn btn-primary">Home</button>
                    </NavLink>
                </div>
            </div>
            <Footer />
        </div>
    )
}

export default FAQ;